body{
    background-image: linear-gradient(#c9c9c9, #eeeeee);
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.navbar-color {
    background-color: #00468E;
}

.nav-link-cl{
    color: #FFF;
    margin: 5px;
}

.nav-link-cl:hover{
    color: #CCC;
    text-decoration: none;
}

h2{
    margin-left: 40px;  
    color: #520063;  
    margin-bottom: 4px;
}

.titulo{
    margin-left: 0px;
    border-bottom: 3px solid #a0a0a0;
    display: inline-block;
    margin-top: 40px;
}

.panel-form{
    margin-top: 40px;
    background-color: #FFF;
    border: 1px solid;
    border-color: #DDD;
    border-radius: 5px;
    padding-top: 30px;
    box-shadow: 0px 0px 3px #CCC;
    padding-bottom: 30px;
}

.sp-top{
    display: block;
    margin-top: 25px;

}